import mapboxgl from 'mapbox-gl'

// create a custom style layer to implement the WebGL content
// https://docs.mapbox.com/mapbox-gl-js/api/properties/#customlayerinterface#render

export class MPoint {

    type: string = 'custom';
    id: string;

    vertexSource = `
    precision highp float;
    uniform mat4 u_matrix;
    uniform float u_size;
    attribute vec2 aPos;

    varying vec2 position;
    
    void main() {
        gl_Position = u_matrix * vec4(aPos, 0.0, 1.0);
        gl_PointSize = u_size; 
        position = vec2(aPos.xy);
    }`;

    fragmentSource = `
    precision mediump float;

    uniform vec4 u_color;
    uniform vec4 u_stroke;

    varying vec2 position;

    void main() {
        
        float aRadius = 2.0;
        float dist = distance(position, gl_FragCoord.xy);

        // https://stackoverflow.com/questions/7237086/opengl-es-2-0-equivalent-for-es-1-0-circles-using-gl-point-smooth
        float len = length(gl_PointCoord - vec2(0.5)) * 1.0;
        float radius = 0.3;
        float strokeWidth = 0.2;

        if (len < radius) {
            gl_FragColor = u_color;
        } else if (len < radius + strokeWidth) {
            gl_FragColor = u_stroke;
        } else {
            discard;
        }
    }`;

    program: WebGLProgram;
    buffer: WebGLBuffer;
    aPos: number;

    data: Float32Array;
    map: mapboxgl.Map;
    constructor(id = 'custom', data: Float32Array) {
        this.id = id;
        this.data = data;
    }

    // method called when the layer is added to the map
    // https://docs.mapbox.com/mapbox-gl-js/api/#styleimageinterface#onadd
    public onAdd(map: mapboxgl.Map, gl: WebGL2RenderingContext) {
        this.map = map;
        // create a vertex shader
        const vertexShader = gl.createShader(gl.VERTEX_SHADER);
        gl.shaderSource(vertexShader, this.vertexSource);
        gl.compileShader(vertexShader);

        // create a fragment shader
        const fragmentShader = gl.createShader(gl.FRAGMENT_SHADER);
        gl.shaderSource(fragmentShader, this.fragmentSource);
        gl.compileShader(fragmentShader);

        // link the two shaders into a WebGL program
        this.program = gl.createProgram();
        gl.attachShader(this.program, vertexShader);
        gl.attachShader(this.program, fragmentShader);
        gl.linkProgram(this.program);

        this.aPos = gl.getAttribLocation(this.program, 'aPos');

        // create and initialize a WebGLBuffer to store vertex and color data
        this.buffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, this.buffer);
        gl.bufferData(gl.ARRAY_BUFFER, this.data, gl.STATIC_DRAW);
    }
    // method fired on each animation frame
    // https://docs.mapbox.com/mapbox-gl-js/api/#map.event:render
    public render(gl: WebGL2RenderingContext, matrix) {
        // console.log(matrix)
        gl.useProgram(this.program);

        // @ts-ignore
        // console.log(this.map.transform)
        // console.log(Math.pow(2.0, this.map.transform.zoom))

        // @ts-ignore
        gl.uniform1f(gl.getUniformLocation(this.program, 'u_size'), 5 * Math.pow(2.0, this.map.transform.zoom));
        gl.uniform4fv(gl.getUniformLocation(this.program, 'u_color'), [1.0, 1.0, 0.0, 1.0]);
        gl.uniform4fv(gl.getUniformLocation(this.program, 'u_stroke'), [1.0, 0.0, 0.0, 1.0]);


        gl.uniformMatrix4fv(gl.getUniformLocation(this.program, 'u_matrix'), false, matrix);
        gl.bindBuffer(gl.ARRAY_BUFFER, this.buffer);
        gl.enableVertexAttribArray(this.aPos);
        gl.vertexAttribPointer(this.aPos, 2, gl.FLOAT, false, 0, 0);
        gl.enable(gl.BLEND);
        gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);
        gl.drawArrays(gl.POINTS, 0, this.data.length * 0.5);
    }
}