
import mapboxgl from 'mapbox-gl'

import { MPoint } from '../Layer/MPoint';
import { MPolygon } from '../Layer/MPolygon';

export class Scene {
    public map: mapboxgl.Map;
    constructor(map: mapboxgl.Map) {
        this.map = map;

        // console.log(this)

    }
    public init() {
        // console.log('init')
        // const customLayer = new CustomWebGLLayer()
        // // @ts-ignore
        // this.map.addLayer(customLayer, 'some-existing-layer-id');
        // console.log(this.map)
        // console.log(this.map.transform)

        // @ts-ignore
        // this.map.addLayer(highlightLayer, 'building');
        // @ts-ignore
        this.map.addLayer(new MPolygon('custom-01'), 'building');
        
        const dataPoint = this.toMercators([25.004, 60.239, 0, 0, 1, 1, 2, 2, 3, 3])
        // console.log(dataPoint);
        // @ts-ignore
        this.map.addLayer(new MPoint('custom-02', dataPoint), 'building');

        // this.map.on('mousemove', 'custom-01', (e) =>{
        //     console.log('customlayer click', e);
        //     });

        this.map.on('click', (e) => {
            // console.log(e)
            // const bbox = [[e.point.x - 20, e.point.y - 20], [e.point.x + 20, e.point.y + 20]];
            // // @ts-ignore
            // const features =this.map.queryRenderedFeatures(bbox, { layers: ['custom-01', 'custom-02'] });
            
            
            const features = this.map.queryRenderedFeatures(e.point, { layers: ['custom-01'] });
            // console.log(features);

            if (features.length > 0) {
              const clickedFeature = features[0];
              // Do something with the clicked feature
              console.log(clickedFeature.properties);
            }
        });

    }

    public dispose() {
        // console.log('dispose', this)
    }
    public toMercator(lng: number, lat: number) {
        return mapboxgl.MercatorCoordinate.fromLngLat({lng, lat})
    }
    public toMercators(data: number[]) {
        const outData = new Float32Array(data.length)
        for(let i = 0 ; i < data.length; i +=2) {
            const projected = this.toMercator(data[i], data[i+1])
            outData[i] = projected.x
            outData[i+1] = projected.y
        }
        return outData;
    }
}
